import React, {Component} from 'react'
import styles from './table.scss'
import classnames from 'classnames'
//接口示例
//titles = ['姓名','语文','数学','英语','总成绩']
//content = {
//    data:[
//        {
//            name:'小明',
//            chinese:'90',
//            ...
//        },
//        {
//            ...
//        }
//    ],
//    keys:['name','chinese',...]
//}
const Titles = (props) => {
    let title = props.title
    let classname = classnames({
        [styles["up-tag"]]:props.flag=='up',
        [styles["down-tag"]]:props.flag == 'down'
    })
    return <th className={classname} onClick={(e)=>props.sort(props.index)}>{title}</th>
}
const Content = (props) => {
    let keys = props.keys
    let data = props.data
    let i = 0
    return (
        <tr>
            {keys.map((key)=>{
                i++
                return <td key={data[key]+i}>{data[key]}</td>
            })}
        </tr>
    )
}
class Table extends Component {
    constructor(props){
        super(props)
        this.sort = this.sort.bind(this)
        this.sortByKey = this.sortByKey.bind(this)
        this.sortsuccess = true
        this.curr = {
            key:'',
            order:'',
            text:''
        }
        this.state = {
            key:'',
            order:'',
            text:''
        }
    }
    sort(index){
        this.sortsuccess = true
        let toKey = this.props.content.keys[index]
        if(this.curr.key != toKey){
            this.curr={
                key:toKey,
                order:'ascending',
                text:this.props.content.titles[index]
            }
        }else{
            this.curr.order = this.curr.order==='ascending'?'descending':'ascending'
        }
        let curr = this.curr
        let data = this.props.content.data
        data.sort(this.sortByKey)
        if(this.sortsuccess){
            this.props.content.data=data
            this.setState((prevState)=>Object.assign({},prevState,curr))
        }
    }
    sortByKey(a,b){
        let key = this.curr.key
        if(!Number(a[key])||!Number(b[key])){
            this.sortsuccess = false
            return false
        }
        if(this.curr.order=='descending'){
            return (Number(b[key])-Number(a[key]))
        }else{
            return (Number(a[key])-Number(b[key]))
        }
    }
    render(){
        let i = 0
        let _ = this
        const obj = this.props.content
        console.log(obj.data)
        const titles = obj.titles.map((value,index)=>{
            let orderFlag = ''
            if(_.state.text == value){
                orderFlag = _.state.order === 'ascending'?'up':'down'
            }
            return <Titles key={'title'+value} title={value} index={index} sort={this.sort} flag={orderFlag}/>
        })
        const content = obj.data.map((item)=>{
            i++
            return <Content key={'line'+i} keys={obj.keys} data={item} />
        })
        return(
            <table>
                <thead>
                    <tr>
                        {titles}
                    </tr>
                </thead>
                <tbody>
                    {content}
                </tbody>
            </table>
        )
    }
}

export default Table


