import React,{Component} from 'react'
import styles from './Sidebar.scss'
import {Link} from 'react-router-dom'
const Items = (props) =>{
    let data = props.data
    return (
        <li><Link to={data.href}>{data.title}</Link></li>
    )
}
class Sidebar extends Component {
    constructor(props){
        super(props)
    }
    render(){
        let list = this.props.list.map((item,index)=>{
            return <Items key={index} data={item}/>
        })
        let direction = {'left':0}
        if(this.props.right){
            direction = {'right':0}
        }
        return (
            <div className={styles.sidebarContainer} style={direction}>
                <ul>
                    {list}
                </ul>
            </div>
        )
    }
}

export default Sidebar


