const reducer = (state=[],action) => {
    switch(action.type){
        case 'GET_LIST':
            let arr = [
                {
                    value: '1',
                    text: '这个',
                },
                {
                    value: '2',
                    text: '那个',
                },
                {
                    value: '3',
                    text: '测试',
                },
            ]
            return Object.assign({},state,{list:arr})
        case 'GET_TODO':
            let todo = [
                {
                    text:'昨天要做的事',
                    completed:false
                },
                {
                    text:'今天要做的事',
                    completed:true
                },
                {
                    text:'明天要做的事',
                    completed:false
                },
            ]
            return Object.assign({},state,{todo:todo})
        case 'ADD_TODO':
            return Object.assign({},state,{todo:state.todo.concat({text:action.text,completed:false})})
        case 'TOGGLE_TODO':
            const prevTodo = state.todo
            prevTodo[action.index].completed = !prevTodo[action.index].completed
            return Object.assign({},state,{todo:prevTodo})
        default:
            return state
    }
}

export default reducer


