import React,{Component} from 'react'
import Select from '../../components/Select'
import {connect} from 'react-redux'
import actions from '../../actions'
import Page from '../../components/Page'
import Table from '../../components/Table'
import Todo from '../Todo'
import Yuebao from '../Yuebao/yuebao'
import {Modal} from 'antd'
class Home extends Component{
    constructor(props){
        super(props)
        this.state = {
            visible:false,
            pages:{
                total:10,
                show_num:8,
                curr:1,
                mousePosition:{
                    x:0,y:0
                }
            }
        }
    }
    goPage(page){
        this.setState((prevState)=>({
            pages: Object.assign({},prevState.pages,{curr:page})
        }))
    }
    showModal(e){
        this.setState({
            visible:true
        })
    }
    onCancel(){
        this.setState({
            visible:false
        })
    }
    render(){
        const {visible} = this.state
        const pages = this.state.pages
        const curr = pages.curr
        const content = {
            titles: ['名字','语文','英语'],
            data:[
                {
                    name:'小明',
                    chinese:'90',
                    math:'82'
                },
                {
                    name:'包子',
                    chinese:'91',
                    math:'88'
                },
                {
                    name:'天天',
                    chinese:'60',
                    math:'99'
                },
            ],
            keys:['name','chinese','math']
        }
console.log(visible)
        return (
            <div>
                <Select list={this.props.list} handleClick={(data)=>console.log(data)} />
                <Page pages={pages} curr={curr} go={(page)=>this.goPage(page)} />
                <div>
                    <Table content={content} />
                </div>
                <Todo />
                <Yuebao />
                <button onClick={(e)=>this.showModal(e)}>弹出</button>
                <Modal visible={visible} title="确认提交吗" onCancel={()=>this.onCancel()}>
                <p>ddd</p>
                </Modal>
            </div>
        )
    }
}
const mapStateToProps = (state,ownProps) => {
    return{
        list:state.home.list
    }
}
const mapDispatchToProps = (dispatch,ownProps) => {
    return{
        getList:()=>dispatch(actions.getSelectOptions())
    }
}
export default connect(mapStateToProps,mapDispatchToProps)(Home)


