import React from 'react'
import Table from '../components/Table'
const table = () =>{
    const content = {
        titles: ['名字','语文','英语'],
        data:[
            {
                name:'小明',
                chinese:'90',
                math:'82'
            },
            {
                name:'包子',
                chinese:'91',
                math:'88'
            },
            {
                name:'天天',
                chinese:'60',
                math:'99'
            },
        ],
        keys:['name','chinese','math']
    }
    return <Table content={content} />
}
export default table


