import React,{Component} from 'react'
import Select from '../../components/Select'
import {connect} from 'react-redux'
import actions from '../../actions'
import styles from './Todo.scss'
class Todo extends Component {
    constructor(props){
        super(props)
        this.state = {
            value:''
        }
    }
    handleChange(e){
        this.setState({
            value:e.target.value
        })
    }
    addTodo(e){
        let value = this.state.value
        if(!value){
            return
        }
        let flag = false
        this.props.data.todo.map((item)=>{
            if(item.text == value){
                flag = true
            }
        })
        if(!flag){
            this.props.add(value)
        }
        this.setState({
            value:''
        })
        e.preventDefault()
    }
    render(){
        const todo = this.props.data.todo
        return (
            <div>
                <form>
                    <input type="text" value={this.state.value} onChange={(e)=>this.handleChange(e)} />
                    <input type="submit" value="提交" onClick={(e)=>this.addTodo(e)} />
                </form>
                <div>
                    <ul className={styles.todoContainer}>
                        {todo.map((item,index)=>{
                            return (
                                <li key={item.text}>
                                    {item.text}
                                        {item.completed}
                                    <div>
                                        <span>{item.completed?'DONE':'UNDO'}</span>
                                        <div className={styles.checkContainer}>
                                            <input className={styles["flex-3"]} id={'todo-'+index} type="checkbox" onClick={()=>this.props.changeStatus(index)} defaultChecked={item.completed} />
                                            <label className={styles.green} htmlFor={'todo-'+index}></label>
                                        </div>
                                    </div>
                                </li>
                            )
                        })}
                    </ul>
                </div>
            </div>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        data: state.home
    }
}
const mapDispatchToProps = (dispatch) => {
    return {
        add: (text)=>dispatch(actions.addTodo(text)),
        changeStatus: (index)=>{
            dispatch(actions.changeStatus(index))
        }
        
    }
}

export default connect(mapStateToProps,mapDispatchToProps)(Todo)


