import React, {Component} from 'react'
import styles from './select.scss'
import classnames from 'classnames'
const Items = (props) => {
    let data = props.data
    return (
        <li onClick={()=>props.handleClick(data)} className={styles.li} data-value={data.value}>{data.text}</li>
    )
}
class Select extends Component {
    constructor(props){
        super(props)
        let defaultCheck = props.defaultCheck||props.list[0]
        this.state = {
            show:false,
            title: defaultCheck.text
        }
    }
    toggle() {
        this.setState(prevState=>({
            show:!prevState.show
        }))
    }
    handleClick(data) {
        this.toggle()
        this.props.handleClick(data)
        this.setText(data.text)
    }
    setText(value){
        this.setState({
            title: value
        })
    }
    render() {
        let defaultValue = (this.props.default||0)
        let list = this.props.list
        const items = list.map((data)=>{
            return <Items handleClick={(data)=>this.handleClick(data)} key={data.value} data={data} />
        })
        return (
            <div tabIndex='10' onBlur={()=>this.setState({show:false})} className={styles['select-container']}>
                <div onClick={()=>this.toggle()}>{this.state.title}</div>
                <ul className={classnames(styles.ul,{
                    [styles.hide]:!this.state.show
                })}>
                    {items}
                </ul>
            </div>
        )
    }
    componentDidMount(){
    }
}

export default Select


