export const getSelectOptions = () => ({
    type: 'GET_LIST'
})

export const getTodoList = () => ({
    type: 'GET_TODO'
})

export const addTodo = (text) => ({
    type: 'ADD_TODO',
    text: text
})

export const changeStatus = (index) => ({
    type: 'TOGGLE_TODO',
    index:index
})


